const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			navList: [],
			list: [],
			params: {
				af_id: '',
				page: 1,
				size: 10
			},
			stime: '',
			ruleDialogs: false,
			shareDialog: false
		}
	},
	//初始化
	onLoad() {
		this.getTypeList()
	},
	// 页面显示
	onShow() {
		
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/panic/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		//获取免费抢购列表
		getTypeList() {
			util.request(api.getZeroBlist,{page: 1, size: 10},'post').then(res=>{
				if(res.code == 0) {
					this.getShareTicket()
					this.navList = res.data.list;
					this.params.af_id = res.data.list[0].af_id;
					//this.stime = res.data.list[0].stime;
					this.getList();
				}
			})
		},
		//获取对应的商品列表
		getList() {
			wx.showLoading({
				title: '加载中...'
			})
			util.request(api.getZeroGlist,this.params,'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					this.list = res.data.list;
				}
			})
		},
		dealPrcture(str) {
			let newStr = str.split(',');
			return newStr[0]
		},
		//抢购类型切换
		typeChange(type,stime) {
			this.params.af_id = type;
			// let ntime = parseInt(new Date().getTime()/1000);
			// this.stime = util.intervalTime(stime, ntime);
			//console.log(this.stime)
			this.getList()
		},
		//活动规则弹框
		ruleDialog() {
			this.ruleDialogs = true;
		},
		close() {
			this.ruleDialogs = false;
		},
		closeShare() {
			this.shareDialog = false;
		},
		//计算百分比
		percentage(total,rnum) {
			return parseInt(((rnum/total)*100))
		},
		//去抢购
		dogo(id,af_id) {
			this.getAdList(id,af_id);
		},
		dogos(id,af_id) {
			var _self = this;
			wx.showLoading({
				mask: true,
				title: '正在抢购中...'
			})
			util.request(api.getZeroGdoGo,{
				id: id,
				af_id: af_id
			},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					wx.showModal({
						title: '完成',
						showCancel: false,
						content: '可点击“我的”查看抢购记录',
						success(res) {
							if(res.confirm) {
								_self.getList()
							}
						}
					})
				}else if(res.code == 413){
					_self.shareDialog = true;
				}else{
					wx.showToast({
						icon: 'none',
						title: res.msg
					})
				}
			})
		},
		//检测收货地址是否为空
		async getAdList(id,af_id) {
			await util.request(api.getAddreList,{},'get').then(res=>{
				if(res.code == 0){
					if(res.data.list && res.data.list.length <=0) {
						 wx.showModal({
							title: '提示',
							showCancel: false,
							content: '暂无收获地址',
							confirmText: '立即前往',
							success(res) {
								if(res.confirm) {
									wx.navigateTo({
										url: '/pages/user/addr'
									})
								}
							}
						})
						return false;
					}else {
						this.dogos(id,af_id)
					}
				}
			})
		},
		openPage(id){
			wx.navigateTo({
				url: '/pages/panic/info?id='+id
			})
		},
		//获取分享数据得到的群信息
		getShareTicket() {
			let ticket = wx.getStorageSync('ticket');
			let pages = getCurrentPages();
			let { options } = pages.pop();
			var that = this;
			if(ticket && options.code != "") {
				wx.getShareInfo({
					shareTicket: ticket,
					success:function(res) {
						let params = {
							code: options.code ? options.code : "",
							iv: res.iv,
							data: res.encryptedData
						}	
						console.log(params)
						that.getShareInfos(params)
					}
				})
			}
		},
		
		//新增分享记录
		getShareInfos(params) {
			util.request(api.setShareRechge, params,'post').then(res=>{
				console.log(res)
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	}
}
